@tailwind base;
@tailwind components;
@tailwind utilities;

@import "./vars.css";
@import url("https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body,
html {
  height: 100vh;
  height: 100svh;
  line-height: 1.5;
}

body {
  font-family: "Ubuntu", sans-serif;
  color: var(--white);
  background: var(--backgroundFallback);
  background: var(--backgroundGradient);
  overflow: hidden;
  display: flex;
}
li {
  list-style: none;
}
#root {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.child {
  margin-right: 2em;
}
